<template>
    <div class="enter"  ref="a">
        <div class="imgs"><img src="https://assets.maizuo.com/h5/mz-auth/public/app/img/logo.19ca0be.png" alt=""></div>
        <div class="sjh"><input type="text" placeholder="手机号" id="phone" maxlength="11"><span>获取验证码</span></div>
        <div class="yzm"><input type="text" placeholder="验证码"  id="password"></div>
        <dir class="dl"><button @click="updata">登录</button></dir>
        <div class="zc"><router-link tag="span" to="./enroll">注册?</router-link></div>
    </div>
</template>
<script>
import { MessageBox } from 'mint-ui';
export default {
    data(){
        return {
            users:[],
            phone:null,
            password:null,
            tos:"###",
            login:"立即登录"
        }
    },
    created(){
        this.$http.get("/api/mz/users").then(res=>{
            this.users = res.data.data.object_list
        })
    },
    computed:{
    },
    methods:{
        updata(){
            let phones = document.getElementById("phone").value
            let passwords = document.getElementById("password").value
            console.log(this.users[0].password)
            for(let i = 0; i < this.users.length; i++){
                if(this.users[i].phone == phones && this.users[i].password == passwords){
                    this.phone = phones;
                    this.password = passwords;
                    let user = {"phone":this.phone,"password":this.password}
                    user = JSON.stringify(user);
                    localStorage.setItem("user",user);
                    MessageBox("提示","登陆成功")
                     this.$router.push("/mine")
                    break;
                }else{
                    MessageBox("提示","登录失败")
                }
            }
        }
    },
}
</script>

<style lang="scss">
.enter{
    padding-top:80px;
    .imgs{
        width: 100%;
        height:60px;
        margin-bottom: 40px;
        text-align: center;
        img{
            height:60px;
            display: inline-block;
        }
    }
    .sjh{
        margin:0 25px;
        overflow: hidden;
        border-bottom: 1px solid #ededed;
        input{
            padding:20px 0;
            height:55px;
            width:235px;
            float: left;
            background: #fff;
            border:none;
        }
        span{
            float: left;
            height: 55px;
            width:90px;
            line-height:55px;
        }
    }
    .yzm{
        margin:0 25px;
        border-bottom: 1px solid #ededed;
        input{
            padding:20px 0;
            height:55px;
            width:325px;
            background:#fff;
            border:none;
        }
    }
    .dl{
        width:100%;
        height:120px;
        padding:70px 25px 0 25px;
        button{
            width:100%;
            height:44px;
            background: #ff5f16;
            border:none;
            border-radius: 5px;
            font-size: 16px;
            color:#fff;
        }
    }
    .zc{
        margin-top:10px;
        overflow: hidden;
        padding:0 25px;
        span{
            float: right;
            font-size: 16px;
            color:#ff5f16;
        }
    }
}
</style>